module.exports = {
	"id": "about",
	"meta": {
		"public": true,
		"title": "About",
		"subtitle": "",
		"keywords": [
			"help",
			"about",
			"readme",
			"instruct",
			"question",
			"site",
			"why",
			"how",
			"example"
		]
	},
	"navbar": {
		"public": true,
		"left": [
			{
				"image": {
					"src": "http://bulma.io/images/bulma-logo.png"
				}
			},
			{
				"content": "<p class=\"title\">About this site</p>"
			}
		],
		"right": [
			{
				"href": "vuex:github.href",
				"icon": "fa fa-github"
			}
		]
	},
	"templates": {},
	"components": [
		"DRender",
		"DLayout",
		"DNavbar",
		"DNavbarItem",
		"DMedia",
		"DSection",
		"DLevel"
	],
	"routes": [
		{
			"path": "/",
			"name": "home",
			"component": "DLayout"
		}
	],
	"database": {
		"user": {},
		"github": {
			"href": "https://github.com/taemon1337/resumer"
		},
		"headings": [
			{
				"component": "d-heading",
				"properties": {
					"heading": "",
					"klass": "title",
					"content": "About this application"
				}
			},
			{
				"component": "d-heading",
				"properties": {
					"heading": "",
					"klass": "title",
					"content": "Center"
				}
			},
			{
				"component": "d-heading",
				"properties": {
					"heading": "",
					"klass": "title",
					"content": "Right"
				}
			}
		]
	},
	"layout": {
		"component": "d-group",
		"properties": {
			"klass": "box",
			"children": [
				{
					"component": "d-level",
					"properties": {
						"items": "vuex:headings"
					}
				}
			]
		}
	},
	"secure": false
};


